import React, { Fragment } from "react";
import { Link, useStaticQuery, graphql } from "gatsby";

import ProfileImg from "../../content/assets/profile-pic.jpg";
import TwitterIconImg from "../../content/assets/Twitter_Logo_Blue.png";
import QiitaIconImg from "../../content/assets/Qiita_Logo.png";
import BackgroundImg from "../../content/assets/bg.jpg";

const Header = ({ title, location }) => {
  const data = useStaticQuery(graphql`
    query HeaderQuery {
      site {
        siteMetadata {
          author {
            name
            summary
          }
          social {
            twitter
            qiita
          }
        }
      }
    }
  `);
  const { author, social } = data.site.siteMetadata;
  const rootPath = `${__PATH_PREFIX__}/`;
  if (location.pathname === rootPath) {
    return (
      <header
        style={{
          backgroundImage: `url(${BackgroundImg})`,
          backgroundSize: "cover",
          backgroundPosition: "center top",
          color: "white",
          width: "100%",
          padding: "108px 0",
        }}
      >
        <div
          style={{
            margin: "0 auto",
            width: "calc(100% - 48px)",
            maxWidth: 480,
            textAlign: "center",
            backgroundColor: "rgba(0, 0, 0, 0.4)",
            padding: "16px 6px",
          }}
        >
          <img
            src={ProfileImg}
            style={{
              width: 108,
              borderRadius: 99999,
              border: "2px solid white",
            }}
          />
          <div
            style={{
              marginTop: 6,
            }}
          >
            <div style={{ fontSize: 18, fontWeight: "bold" }}>
              {author.name}
            </div>
            <div style={{ fontSize: 16, marginTop: 2, marginBottom: 8 }}>
              {author.summary}
            </div>
            <div style={{ fontSize: 14 }}>
              {[
                {
                  url: `https://twitter.com/${social.twitter}`,
                  icon: TwitterIconImg,
                  name: "Twitter",
                },
                {
                  url: `https://qiita.com/${social.qiita}`,
                  icon: QiitaIconImg,
                  name: "Qiita",
                },
              ].map((social, i) => (
                <Fragment>
                  {i !== 0 && "/"}
                  <div
                    key={i}
                    style={{
                      display: "inline-block",
                      margin: "0 4px",
                    }}
                  >
                    <a
                      href={social.url}
                      style={{
                        color: "white",
                      }}
                    >
                      {social.name}
                    </a>
                  </div>
                </Fragment>
              ))}
            </div>
          </div>
        </div>
      </header>
    );
  }
  return (
    <header
      style={{
        textAlign: "center",
        padding: "20px 0",
        backgroundColor: "blue",
      }}
    >
      <Link
        style={{
          textDecoration: "none",
          fontSize: 36,
          color: "white",
        }}
        to={`/`}
      >
        {title}
      </Link>
    </header>
  );
};

export default Header;
